import { ProfileAPI, userApi } from "../API/api";

const ADD_POST = "ADD-POST"; // ключ, для проверки, как нужно изменить стейт
const SET_USER_PROFILE = "SET-USER-PROFILE";
const CHANGE_STATUS = "CHANGE_STATUS";

//создание базового стейта, при первом запуске проходит инициализация стейта, для этого создается
//базовое состояние каждой ветки стейта
let initState = {
  postsData: [
    { message: "It's my first post", like: "11" },
    { message: "Hello, how are you?", like: "12" },
  ],
  currentProfile: null,
  status: "",
};

//Reducer, метод изменяющий состояние стейта, в себя принимает текущее состояние стейта
//при запуске программы происходит инициализация стейта редьюсерами и тк текущего стейта еще нет,
//параметру текущего стейта задается базовое состояние ветки стейта
//в качестве второго параметра редьюсер принимает action - ключ, как нужно изменить стейт
const profileReducer = (state = initState, action) => {
  switch (action.type) {
    case ADD_POST: {
      let newPost = { ...action.message, like: "0" };
      return {
        ...state,
        postsData: [...state.postsData, newPost],
        newPostText: "",
      };
    }
    case SET_USER_PROFILE: {
      return {
        ...state,
        currentProfile: { ...action.profile },
      };
    }
    case CHANGE_STATUS: {
      return {
        ...state,
        status: action.status,
      };
    }
    default:
      return state;
  }
};

//Создание параметров для метода dispatch
//dispatch просходит по всем редьюсерам с проверкой ключа
//параметры принимаются редьюсерами в качестве action
export const addPost = (message) => ({ type: ADD_POST, message });
export const setUserProfile = (profile) => ({
  type: SET_USER_PROFILE,
  profile,
});
export const changeStatus = (status) => ({
  type: CHANGE_STATUS,
  status,
});

//Thunk для диспатча функции
export const setProfileThunk = (userId) => {
  return async (dispatch) => {
    let response = await userApi.getUserProfile(userId);
    dispatch(setUserProfile(response.data));
  };
};
export const setStatus = (userId) => {
  return async (dispatch) => {
    let response = await ProfileAPI.getStatus(userId);
    dispatch(changeStatus(response.data));
  };
};
export const updateStatus = (status) => {
  return async (dispatch) => {
    let response = await ProfileAPI.updateStatus(status);
    if (response.data.resultCode === 0) {
      dispatch(changeStatus(status));
    }
  };
};

//экспорт для склеивания store
export default profileReducer;
