import * as axios from "axios";
import { logout } from "../redux/auth_reducer";

const instance = axios.create({
  withCredentials: true, //куки(при кроссдоменности)
  baseURL: "https://social-network.samuraijs.com/api/1.0/",
  headers: {
    "API-KEY": "f638e249-cfb5-4d84-9256-0d3e63f7af0c",
  },
});

export const userApi = {
  getUsers(pageNumber = 1, pageSize = 10) {
    return instance
      .get(`users?page=${pageNumber}&count=${pageSize}`)
      .then((response) => {
        return response.data;
      });
  },
  getUserProfile(userId) {
    console.warn("Obsolete method. Please use ProfileAPI object.");
    return ProfileAPI.getUserProfile(userId);
  },
};

export const changeSubscriptionAPI = {
  followAPI(userId) {
    return instance.post(`follow/${userId}`);
  },
  unfollowAPI(userId) {
    return instance.delete(`follow/${userId}`);
  },
};

export const authAPI = {
  check() {
    return instance.get(`auth/me`);
  },
  login(email, password, rememberMe, captcha) {
    return captcha ? instance.post(`auth/login`, { email, password, rememberMe, captcha }) 
    :
    instance.post('auth/login', {email, password, rememberMe})
  },
  logout() {
    return instance.delete(`auth/login`);
  },
  getCaptchaURL() {
    return instance.get('/security/get-captcha-url');
  },
};

export const ProfileAPI = {
  getUserProfile(userId) {
    if (userId) {
      return instance.get(`profile/${userId}`);
    }
  },
  getMeProfile() {
    return instance.get(`profile/me`);
  },
  getStatus(userId) {
    return instance.get(`profile/status/${userId}`);
  },
  updateStatus(status) {
    return instance.put(`profile/status`, { status });
  },
};
