import s from './navbar.module.css'
import {NavLink} from 'react-router-dom'
import logo512 from "./../../Assets/images/logo512.png";

const NavBar = () => {
    return (
        <nav className={`${s.nav} ${s.padding}`}>
            <div>
                <NavLink className = {s.item} to='/profile' activeClassName={s.item_active}>Profile</NavLink>
            </div>
            <div>
                <NavLink className = {s.item} to='/dialogs' activeClassName={s.item_active}>Message</NavLink>
            </div>
            <div>
                <NavLink className = {s.item} to='/news' activeClassName={s.item_active}>News</NavLink>
            </div>
            <div>
                <NavLink className = {s.item} to='/music' activeClassName={s.item_active}>Music</NavLink>
            </div>
            <div>
                <NavLink className = {s.item} to='/users' activeClassName={s.item_active}>Users</NavLink>
            </div>
            <div>
                <h3 className = {s.itemFriends}>Friends</h3>
            </div>
            <div>
                <div>
                    <img className={s.imgUser} src={logo512}></img>
                    <img className={s.imgUser} src={logo512}></img>
                </div>
                <div>
                    <NavLink className = {s.friendsElement} to='/dialogs/enemy'>enemy</NavLink>
                    <NavLink className = {s.friendsElement} to='/dialogs/spyspy'>spypsy</NavLink>
                </div>
            </div>
            <div className={s.settings}>
                <NavLink className = {s.item} to='/settings' activeClassName={s.item_active}>Settings</NavLink>
            </div>
        </nav>
    )
}

export default NavBar;