import { applyMiddleware, combineReducers, createStore } from "redux";
import authReducer from "./auth_reducer";
import dialogsReducer from "./dialogs_reducer";
import profileReducer from "./profile_reducer";
import usersReducer from "./users_reducer";
import thunkMiddleware from "redux-thunk"
import appReducer from "./app_reducer";
//Использование редакса
//Reducers - методы изменяющие состояние стейта
//нужно самому создать концепцию стейта, самому написать самостоятельные редьюсеры
//после они соединяются и создается стейт с параметром редьюсеров


//В данном случае задается ветка стейта(ключ): редьюсер стейта(значение)
let reducers = combineReducers({
    profilePage: profileReducer,
    dialogsPage: dialogsReducer,
    usersPage: usersReducer,
    auth: authReducer,
    initialApp: appReducer
})

let store = createStore(reducers, applyMiddleware(thunkMiddleware));

window.store = store

export default store;