import { userApi, changeSubscriptionAPI } from "../API/api";
import { updateObjectInArray } from "../utilities/object-helper";

const FOLLOW = "FOLLOW"; // ключ, для проверки, как нужно изменить стейт
const UNFOLLOW = "UNFOLLOW"; // ключ, для проверки, как нужно изменить стейт
const SET_USERS = "SET_USERS"; // ключ, для проверки, как нужно изменить стейт
const SET_CURRENT_PAGE = "SET_CURRENT_PAGE"; // ключ, для проверки, как нужно изменить стейт
const SET_TOTAL_USERS_COUNT = "SET_TOTAL_USERS_COUNT"; // ключ, для проверки, как нужно изменить стейт
const TOGGLE_IS_FETCHING = "TOGGLE_IS_FETCHING"; // ключ, для проверки, как нужно изменить стейт
const TOGGLE_IS_FOLLOWING_PROGRESS = "TOGGLE_IS_FOLLOWING_PROGRESS"; // ключ, для проверки, как нужно изменить стейт

//создание базового стейта, при первом запуске проходит инициализация стейта, для этого создается
//базовое состояние каждой ветки стейта
let initState = {
  users: [],
  pageSize: 6,
  totalUsersCount: 0,
  currentPage: 1,
  isFetching: true,
  followingInProgress: [],
};

//Reducer, метод изменяющий состояние стейта, в себя принимает текущее состояние стейта
//при запуске программы происходит инициализация стейта редьюсерами и тк текущего стейта еще нет,
//параметру текущего стейта задается базовое состояние ветки стейта
//в качестве второго параметра редьюсер принимает action - ключ, как нужно изменить стейт
const usersReducer = (state = initState, action) => {
  //Тот объект которые передается в компоненту должен быть заменен, глубокое копирование
  //работать не будет если если дочерние объекты компоненты будут заменены
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userID, "id", {
          followed: true,
        }),
        // state.users.map((u) => {
        //   if (u.id === action.userID) {
        //     return { ...u, followed: true };
        //   }
        //   return u;
        // }),
      };
    case UNFOLLOW:
      debugger
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userID, "id", {
          followed: false,
        }),
        // state.users.map((u) => {
        //   if (u.id === action.userID) {
        //     return { ...u, followed: false };
        //   }
        //   return u;
        // }),
      };
    case SET_USERS:
      return {
        ...state,
        users: action.users,
      };
    case SET_CURRENT_PAGE:
      return {
        ...state,
        currentPage: action.currentPage,
      };
    case SET_TOTAL_USERS_COUNT:
      return {
        ...state,
        totalUsersCount: action.totalCount,
      };
    case TOGGLE_IS_FETCHING:
      return {
        ...state,
        isFetching: action.isFetching,
      };
    case TOGGLE_IS_FOLLOWING_PROGRESS:
      return {
        ...state,
        followingInProgress: action.isFetching
          ? [...state.followingInProgress, action.userId]
          : state.followingInProgress.filter((id) => id != action.userId),
      };
    default:
      return state;
  }
};

//Создание параметров для метода dispatch
//dispatch просходит по всем редьюсерам с проверкой ключа
//параметры принимаются редьюсерами в качестве action
export const followSuccess = (userID) => ({ type: FOLLOW, userID });
export const unfollowSuccess = (userID) => ({ type: UNFOLLOW, userID });
export const setUsers = (users) => ({ type: SET_USERS, users });
export const setCurrentPage = (currentPage) => ({
  type: SET_CURRENT_PAGE,
  currentPage,
});
export const setTotalUsersCount = (totalCount) => ({
  type: SET_TOTAL_USERS_COUNT,
  totalCount,
});
export const toggleIsFetching = (isFetching) => ({
  type: TOGGLE_IS_FETCHING,
  isFetching,
});
export const toggleFollowingProgress = (isFetching, userId) => ({
  type: TOGGLE_IS_FOLLOWING_PROGRESS,
  isFetching,
  userId,
});

//Thunk для диспатча функции
export const getUsersThunkCreator = (page, size) => {
  return async (dispatch) => {
    dispatch(toggleIsFetching(true));
    let data = await userApi.getUsers(page, size);
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));
    dispatch(toggleIsFetching(false));
  };
};
export const getUsersThunkCreatorWithCurrentPage = (currentPage, size) => {
  return async (dispatch) => {
    dispatch(toggleIsFetching(true));
    dispatch(setCurrentPage(currentPage));
    let data = await userApi.getUsers(currentPage, size);
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));
    dispatch(toggleIsFetching(false));
  };
};
export const follow = (userId) => {
  return async (dispatch) => {
    followUnfollowFlow(
      dispatch,
      userId,
      changeSubscriptionAPI.followAPI.bind(userId),
      followSuccess
    );
  };
};
export const unfollow = (userId) => {
  return async (dispatch) => {
    followUnfollowFlow(
      dispatch,
      userId,
      changeSubscriptionAPI.unfollowAPI.bind(userId),
      unfollowSuccess
    );
  };
};

const followUnfollowFlow = async (
  dispatch,
  userId,
  apiMethod,
  actionCreator
) => {
  debugger
  dispatch(toggleFollowingProgress(true, userId));
  let response = await apiMethod(userId);
  debugger
  if (response.data.resultCode === 0) {
    debugger
    dispatch(actionCreator(userId));
  }
  dispatch(toggleFollowingProgress(false, userId));
};

//экспорт для склеивания store
export default usersReducer;
