import { ErrorMessage } from "formik";
import { authAPI } from "../API/api";

const SET_USER_DATA = "social-network/auth/SET_USER_DATA"; // ключ, для проверки, как нужно изменить стейт
const SET_CAPTCHA = "SET_CAPTCHA";

//создание базового стейта, при первом запуске проходит инициализация стейта, для этого создается
//базовое состояние каждой ветки стейта
let initState = {
  userId: null,
  email: null,
  login: null,
  isAuth: false,
  captchaURL: null,
};

//Reducer, метод изменяющий состояние стейта, в себя принимает текущее состояние стейта
//при запуске программы происходит инициализация стейта редьюсерами и тк текущего стейта еще нет,
//параметру текущего стейта задается базовое состояние ветки стейта
//в качестве второго параметра редьюсер принимает action - ключ, как нужно изменить стейт
const authReducer = (state = initState, action) => {
  //Тот объект которые передается в компоненту должен быть заменен, глубокое копирование
  //работать не будет если если дочерние объекты компоненты будут заменены
  switch (action.type) {
    case SET_USER_DATA:
      return {
        ...state,
        ...action.data,
      };
    case SET_CAPTCHA:
      return {
        ...state,
        captchaURL: action.url,
      };
    default:
      return state;
  }
};

//Создание параметров для метода dispatch
//dispatch просходит по всем редьюсерам с проверкой ключа
//параметры принимаются редьюсерами в качестве action
export const setAuthUserData = (userId, email, login, isAuth) => ({
  type: SET_USER_DATA,
  data: { userId, email, login, isAuth },
});
export const setCaptchaURL = (url) => ({
  type: SET_CAPTCHA,
  url,
});

//Thunk для диспатча функции
export const checkAuth = () => async (dispatch) => {
  let response = await authAPI.check();
  if (response.data.resultCode === 0) {
    let { id, email, login } = response.data.data;
    dispatch(setAuthUserData(id, email, login, true));
  }
};
export const login = (
  email,
  password,
  onSubmitProps,
  captcha,
  rememberMe
) => async (dispatch) => {
  let response = await authAPI.login(email, password, rememberMe, captcha);
  if (response.data.resultCode === 0) {
    onSubmitProps.setSubmitting(false);
    dispatch(checkAuth());
  }
  if (response.data.resultCode === 1) {
    onSubmitProps.setSubmitting(false);
    onSubmitProps.setErrors({
      username: "emial or password is wrong",
      password: "email or password is wrong",
    });
  }
  if (response.data.resultCode === 10) {
    dispatch(getCaptcha(onSubmitProps));
  }
};

export const getCaptcha = (onSubmitProps) => async (dispatch) => {
  let response = await authAPI.getCaptchaURL();
  dispatch(setCaptchaURL(response.data.url));
  onSubmitProps.setSubmitting(false);
  onSubmitProps.setErrors({
    username: "enter the captcha",
    password: "enter the captcha",
  });
  onSubmitProps.setSubmitting(false);
};

export const logout = () => async (dispatch) => {
  let response = await authAPI.logout();
  if (response.data.resultCode === 0) {
    dispatch(setAuthUserData(null, null, null, false));
  }
};

//экспорт для склеивания store
export default authReducer;
