import React from 'react'
import { useForm } from 'react-hook-form'
import { Formik, Form, Field, ErrorMessage } from 'formik'
import * as Yup from 'yup'
import s from './login.module.css'
import textError from '../common/textError'
import { connect } from 'react-redux'
import { login } from '../../redux/auth_reducer'
import { Redirect } from 'react-router-dom'

const Login = (props) => {

    if (props.isAuth) {
        return (<Redirect to='/profile' />)
    }

    return (
        <div>
            <h1>Authorization</h1>
            <LoginForm login={props.login} captchaURL={props.captchaURL} />
        </div>
    )
}

const LoginForm = (props) => {

    const initialValues = {
        username: '',
        password: '',
        captcha: ''
    }

    let validationSchema = Yup.object({
        username: Yup.string().required('Required'),
        password: Yup.string().required('Required'),
    })

    const onSubmit = (values, onSubmitProps) => {
        onSubmitProps.setSubmitting(true)
        if (props.captchaURL) {
            props.login(values.username, values.password, onSubmitProps, values.captcha)
        }
        else {
            props.login(values.username, values.password, onSubmitProps)
        }
    }

    if (props.captchaURL) {
        validationSchema = Yup.object({
            username: Yup.string().required('Required'),
            password: Yup.string().required('Required'),
            captchaURL: Yup.string().required('Enter the captcha')
        })
    }

    return (
        <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={onSubmit}>
            {formik => {
                return (
                    <Form id='authorization' name='authorization'>
                        <div>
                            <div className={s.label}>
                                <h1 /> UserName
                             </div>
                            <div>
                                <Field type='text' id='username' name='username' />
                                <ErrorMessage name='username' component={textError} />
                            </div>
                            <div className={s.label}>
                                <h1 /> Password
                                </div>
                            <div>
                                <Field type='password' id='password' name='password' />
                                <ErrorMessage name='password' component={textError} />
                            </div>
                            <div>
                                <Field type='checkbox' id='checkbox' name='checkbox' />Remember me
                                </div>
                            {props.captchaURL &&
                                <div>
                                    <div>
                                        <img src={props.captchaURL} />
                                    </div>
                                    <div>
                                        <Field type='text' id='captcha' name='captcha' />
                                        <ErrorMessage name='captcha' component={textError} />
                                    </div>
                                </div>
                            }
                            <div>
                                <button type='submit' disabled={formik.isSubmitting}>Login</button>
                                <button>Forgot password</button>
                            </div>
                        </div>
                    </Form>
                )
            }}
        </Formik>
    )
}

const mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth,
        captchaURL: state.auth.captchaURL
    }
}

export default connect(mapStateToProps, { login })(Login)


