import { NavLink } from "react-router-dom";
import logo512 from "./../../Assets/images/logo512.png";
import s from './header.module.css'

const Header = (props) => {

    const logout = () => {
        props.logout()
    }

    return (
        <header className={s.header}>
            <span>
                <img className={s.img} src={logo512} />
            </span>
            <span>
                {props.isAuth ?
                    <div className={s.loginItem} >
                        <div>
                            <NavLink className={s.login} to={'/profile/' + props.userId}>
                                {props.login}
                            </NavLink>
                        </div>
                        <div>
                            <button onClick={logout}>
                                Logout
                            </button>
                        </div>
                    </div>
                    :
                    <NavLink className={s.unlogin} to='/login'>
                        Login
                    </NavLink>}
            </span>
        </header>
    )
}

export default Header;

